/* Project CSS */

.card {
    position: absolute;
    width: 350px;
    height: 350px;
    background-color: #000;
}

.card .image {
    width: 350px;
    height: 350px;
    overflow: hidden;
}

.card .image img {
    width: 350px;
    height: 350px;
    transition: .5s;
}

.card:hover .image img {
    opacity: .5;
    -webkit-transform: translateX(30%);
            transform: translateX(30%);
}

.card .details {
    position: absolute;
    top: 0;
    left: 0;
    width: 70%;
    height: 350px;
    background: #fb7820;
    transition: .5s;
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-transform: perspective(2000px) rotateY(-90deg);
            transform: perspective(2000px) rotateY(-90deg);
}

.card:hover .details {
    -webkit-transform: perspective(2000px) rotateY(0deg);
            transform: perspective(2000px) rotateY(0deg);
}

.card .details .center {
    padding: 20px;
    background-color: #000;
    width: 100%;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.card .details .center h1 {
    margin: 0;
    padding: 0;
    color: #fb7820;
    line-height: 20px;
    font-size: 20px;
    text-transform: uppercase;
    text-align: center;
}

.card .details .center h1 span {
    font-size: 12px;
    color: #fff;
}

.card .detail .center p {
    margin: 10px 0;
    padding: 0;
    color: #fff;
    font-size: 8px;
}


